import React from "react";
import { Container, Row, Col } from "reactstrap";

const renderBubbles = () => {
  let bubbles = [];
  const items = [
    { icon: "salesforce", name: "Salesforce" },
    { icon: "hubspot", name: "HubSpot" },
    { icon: "odoo", name: "Odoo" },
    { icon: "sap", name: "SAP" },
    { icon: "powerbi", name: "PowerBI" },
    { icon: "tableau", name: "Tableau" },
    { icon: "slack", name: "Slack" },
    { icon: "teams", name: "Microsoft Teams" },
  ];

  items.forEach((bubble, i) => {
    const iconSize =
      i % 4 === 0 ? "xl" : i % 3 === 0 ? "lg" : i % 2 === 0 ? "lg" : "xxl";

    bubbles.push(
      <div
        key={i}
        className={`bubble bg-contrast rounded-circle p-2 shadow icon icon-${iconSize}`}
      >
        <img
          src={require(`assets/img/demo/header/${bubble.icon}.svg`)}
          alt=""
          className="img-responsive"
        />
        <span className="badge badge-contrast shadow-box">{bubble.name}</span>
      </div>
    );
  });

  return bubbles;
};

const Integration = () => {
  return (
    <section className="section integration-bubbles">
      <Container className="overflow-hidden">
        <Row>
          <Col md="6">
            <div className="bubbles-wrapper">
              <div className="animations m-0">{renderBubbles()}</div>
            </div>
          </Col>

          <Col md="6">
            <div className="section-heading">
              <p className="text-alternate text-uppercase bold">
                Save time by reusing prebuilt integrations
              </p>
              <h2 className="heading-line">Integrate multiple cloud services and communication platforms</h2>
              <p className="lead">
                Give your users a global view of your data by combining multiple cloud services, supporting SalesForce, HubSpot, Odoo, SAP, PowerBI and Tableau.
              </p>
              <p className="lead">
                Integrate your organization communication platforms so users can talk with your assistants everywhere. Slack and Microsoft Teams supported out of the box.
              </p>
            </div>
          </Col>
        </Row>
      </Container>
    </section>
  );
};

export default Integration;
